@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body{
    margin: 0px;
    overflow:inherit!important;
    padding-right: 0px!important;
}


.navbar.navbar-dark {
    background: #6c6c6c!important;
    padding: 20px;
    height: 90px;
}

#offcanvas-subtitle{
    color: black;
    text-decoration: none;
}

.offcanvas-start {
    top: 0;
    left: 0;
    width: 250px;
    border-right: 1px solid rgba(0,0,0,.2);
    transform: translateX(-100%);
}

.container--title {
    font-family: 'Press Start 2P', cursive;
    text-align: center;
    margin-top: 30px;
    margin-bottom: 20px;
}

.container--pokemones{
    width: 350px;
    margin: 0px auto;
}



ul {
    margin-top: 20px;
    display: inline-flex;

}

li img {
    width: 140px;
    height: 165px;
    align-self: center;
    background: #cdcdcd70;
}

ul li {
    width: inherit;
    list-style: none;
    margin-top: 20px;
    margin-left: 33px;
    border: 2px solid #bdbdbd;
    border-radius: 5px;
}
li p{
    background: #0020ff;
    text-align: center;
    color: white;
}


.notfound{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
}
.notfound h2{
    font-style: oblique;
    color: #a7a7a7;
}
.notfound img{
    width: 80px;
}

.contenedorDetails
{
    background-image: url(https://images7.alphacoders.com/662/662102.png);
    background-size: 105%;
}


/*AGREGAR*/
/*CARDS*/
#ul-movies
{
  display: inline-flex;
}

#ul-movies li {
  color: black;
  width: inherit;
  list-style: none;
  margin-top: 20px;
  margin-left: 33px;
}

/*ADD MOVIES*/
#title-addMovies {
    color: black;
}
.mb-3{
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.mb-3 input{
  width: 50%;
  border: none;
  border-radius: 5px;
}

#form-addMovie {
  display: flex;
  width: 60%;
  margin: 0px auto;
  -ms-flex-direction: column;
  flex-direction: column;
}

#form-addMovie label {
  color: black;
  font-size: 18px;
  text-align: initial;
}

#fileSelector{
  margin-top: 10px;
}


/**APP TAKS**/
.App-logo {
    width: 35%;
}
#img-Moviecard {
  width: 191px;
  margin-left: -29px;
  height: 185px;
}
#desc-moviesCard{
  text-align: justify;
}
.mt-4 {
    margin-top: -0.5rem!important;
}